<template>
    <v-footer
        color="#111419"
        dark
        class="d-flex flex-column justify-center onepage-footer">
            <div class="my-5">
                <v-btn
                    v-for="icon in icons"
                    :key="icon"
                    class="scroll-out-item-bottom-sp mx-4"
                    icon>
                    <v-icon size="30px" color="#abb2ba">{{ icon }}</v-icon>
                </v-btn>
            </div>
            <p class="onepage-footer-text scroll-out-item-center">
                Copyright  {{ new Date().getFullYear() }} 
                <span style="color: #e84189; font-weight: 700;">Jhon Doe</span> - Design: JProject
            </p>
            <div class="d-flex">
                <span class="text-capitalize mx-4">term & policy</span>
                <span class="text-capitalize mx-4">disclaimer</span>
            </div>
    </v-footer>
</template>

<script>
import ScrollOut from "scroll-out";
    export default {
        name: 'Footer',

        mounted() {
            this.so = ScrollOut({
                scope: this.$el,
                once: true,
                threshold: 0.5,
                targets:
                    ".scroll-out-item-center, .scroll-out-item-bottom-sp",
            });
        },
        
        destroyed() {
            this.so.teardown();
        },

        data: () => ({
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
        }),
    }
</script>

<style scoped>
    .onepage-footer {
        height: 40vh;
    }
    .onepage-footer-text {
        font-family: 'Poppins', sans-serif;
        font-weight: 600;
        word-spacing: 0.4em;
    }
    @media only screen and (max-width: 960px) {
        .onepage-footer {
            text-align: center;
        }
    }
</style>